import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";
import Elon_musk_img from "./Images/Elon_musk.jpg";
import Jeff_bezos_img from "./Images/Jeff_bezos.jpg";
import TinderCard from "react-tinder-card";
import axios from "axios";

const TinderCards = () => {
   const localhost = "http://localhost:3005";
   const server = "https://tinder-backend2001.herokuapp.com";
   const [people, setPeople] = useState([]);

   useEffect(() => {
      async function fetchData() {
         const req = await axios.get(`${server}/card`);
         setPeople(req.data);
      }

      fetchData();
   }, []);

   const swiped = (name) => {
      console.log(`removing ${name}`);
   };
   const swipedOut = (name) => {
      console.log(`removed ${name} from the screen`);
   };
   const [cardExist, setCardExist] = useState(true);
   return (
      <>
         <div className="tinderCards">
            {people.map((obj) => {
               return (
                  <TinderCard
                     className="swipe"
                     key={obj.name}
                     preventSwipe={["up", "down"]}
                     onSwipe={() => {
                        swiped(obj.name);
                     }}
                     onCardLeftScreen={() => {
                        swipedOut(obj.name);
                     }}
                  >
                     <div className="tinderCards__wrapper">
                        <div
                           className="tinderCards__img col-12 col-md-10 col-lg-6"
                           style={{ backgroundImage: `url('${obj.imgUrl}')` }}
                        >
                           <h3 className="tinderCards__name">{obj.name}</h3>
                        </div>
                     </div>
                  </TinderCard>
               );
            })}
         </div>
      </>
   );
};
export default TinderCards;
